export default{
	data(){
		return{
			userInfo: {},
			hasUserInfo: false
		}
	},
	onLoad() {
		
	},
	onShow() {
	},
	methods:{
		mGetUserInfo:function(res){
			console.log("**: " + JSON.stringify(res.detail.userInfo));
			if (res.detail.errMsg !== 'getUserInfo:ok') {
				uni.showModal({
					title: '获取用户信息失败',
					content: '错误原因' + res.detail.errMsg,
					showCancel: false
				});
				return;
			}
			this.hasUserInfo = true;
			this.userInfo = res.detail.userInfo;
		}
	}
}
